import { Flex, Text, Spacer, Icon, Center,Box, Input} from "@chakra-ui/react";
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';
import { MdPower } from "react-icons/md";

interface ChargerFieldCard2Props {
  chargerName: string;
  provideMethod: string;
  amps: number;
  num :number;
  onRemoveHandler: () => void,
  onClick?: (chargerName: string) => void;
}

const ChargerFieldCard2 = (props: ChargerFieldCard2Props) => {
  const { 
    chargerName,
    provideMethod, 
    amps,
    num, 
    onClick, 
    onRemoveHandler} = props;

  return (
    <Center
      w="267px"//267
      h="144px"//200
      flexDirection="column"
      boxShadow="outline"
      style={{
        boxShadow: "0px 4px 5px rgba(0, 0, 0, 0.5)",
      }}
      onClick={() => {
        if (onClick !== undefined) {
          onClick(chargerName);
        }
      }}
    >
      <Flex direction="row" alignItems="center">
        <Icon marginTop="10px" w="30px" h="30px" as={MdPower} />
        <Text fontSize="18px" fontWeight="bold" marginTop="10px">
          {chargerName}
        </Text>
      </Flex>
      <Flex w="225px" direction="column" fontSize="12px" marginTop="10px">
        <Flex direction="row">
          <Text>電源供給方法：</Text>
          <Text fontWeight="bold">{provideMethod}</Text>
        </Flex>
        <Flex direction="row">
          <Text>電源アンペア数：</Text>
          <Text fontWeight="bold">{`${amps}A`}</Text>
        </Flex>
      </Flex>
      <Center
        marginRight="10px"
        //visibility={isNum ? "visible" : "hidden"}
      >
        <Flex direction="row">
          <Input
            w="50px"
            defaultValue={num || "1"}
            isDisabled = {true}
          />
          <Box
            marginLeft="10px"
            marginTop="5px"
          >
            <DeleteForeverIcon
              onClick={() => {
                console.log("remove!")
                onRemoveHandler();
              }}
            />
          </Box>
        </Flex>
      </Center>
    </Center>
  );
};

export default ChargerFieldCard2;
