import React from 'react';
import { useHistory } from 'react-router-dom';
import axios from 'axios';
import {
    Center,
    Text,
    Flex,
    Box,
    Button,
    Spacer,
    Divider,
} from '@chakra-ui/react';

import {apiURL,Contracts,BatteryOption,Charger,DateFormat,formatDate} from "../../modules/interfaces"

import Header from "../../components/Header"
import HeaderSub from "../../components/HeaderSub"
import NameCard from "../../components/cards/NameCard";
import CarMiniCard2 from "../../components/cards/CarMiniCard2";
import ChargerMiniCard2 from "../../components/cards/ChargerMiniCard2";
import SubmitContractCard from "../../components/cards/SubmitContractCard";
import { valueScaleCorrection } from 'framer-motion/types/render/dom/projection/scale-correction';
import StatusBar from "../../components/StatusBar"
import ContractCard from 'components/components2/ContractCard';
import { contextType } from 'qrcode.react';
import ContractCardNoClick from 'components/components2/ContractCardNoClick';

import ChargerFieldCard3 from 'components/components2/ChargerFieldCard3';
import VehicleFieldCard3 from 'components/components2/VehicleFieldCard3';

interface ContractRegistrationCompleteView2PageProps {
    location: {
        state:{
            company: string,
            officeName: string,
            contractName: string,
            contract_id: number,
            account_id: number,
            department_id: number,
            battery_option_id: number [],
            charger_id: number [],
            charger_name: string [],
            option_name: string [],
            carName?: string,
            chargerName?: string,
            option_num?: number [],//battery
            charger_num?: number [],
        }
    }
}

const ContractRegistrationCompleteView2Page = (props: ContractRegistrationCompleteView2PageProps) => {
    const { location } = props;
    const company = location.state.company;
    const officeName = location.state.officeName;
    const contractName = location.state.contractName;
    const contract_id = location.state.contract_id;
    const carName = location.state.carName || "";
    const chargerName = location.state.chargerName || "";

    const account_id = location.state.account_id;
    const department_id = location.state.department_id;
    const battery_option_id = location.state.battery_option_id || [];
    const charger_id = location.state.charger_id || [];
    const charger_name = location.state.charger_name || [];
    const option_name = location.state.option_name || [];
    const option_num = location.state.option_num || [];
    const charger_num = location.state.charger_num || [];

    const noremove = true;
    const noheader = true;

    const history = useHistory();

    console.log("contract_is is")
    console.log(contract_id)
    console.log("battery_option_id & charger_id")
    console.log(battery_option_id)
    console.log(charger_id)

    //battery_idおよびcharger_idそれぞれのnameを持つ配列を作成
    interface name {
        this_name: string,
        this_idx: number,
        this_num: number,
    }
    let batterys_name :name [] = []
    let chargers_name :name [] = []

    for (let i=0;i<battery_option_id.length;i++){
        //let tmp :name = {this_name:"車体"+String(battery_option_id[i])} 
        let tmp :name = {this_name:option_name[i],this_idx:i,this_num:option_num[i]||1}
        batterys_name.push(tmp) 
    }
    for (let i=0;i<charger_id.length;i++){
        //let tmp :name = {this_name:"充電器"+String(charger_id[i]),this_idx:i}//要検討
        let tmp :name = {this_name:charger_name[i],this_idx:i,this_num:charger_num[i]||1}
        chargers_name.push(tmp)
    }

    //契約情報を取得
    const defaultContracts:Contracts[] = [{contract_id:0,contract_name:"",department_id:0,contract_type:"",execution_date:0,expiration_date:0}]
    const [contracts, setContracts]: [Contracts[], (contracts: Contracts[]) => void] = React.useState(defaultContracts);
    React.useEffect(() => {
        axios
            .get<Contracts[]>(apiURL+"api/v1/contract/?contract_id="+String(contract_id), {
                headers: {
                    "Content-Type": "application/json"
                },
            })
            .then(response => {
                setContracts(response.data);
            });
        }, []);
        console.log('contract data should be fetched at this point');
        console.log(contracts)
    
    let getContracts:Contracts[] = []
    if(contracts!=null && contracts.length>0){
        getContracts=contracts
    }

    //battery optionsのうち、department idのものを取得（複数）
    const defaultOptions:BatteryOption[] = [];
    const [options, setOptions]: [BatteryOption[], (options: BatteryOption[]) => void] = React.useState(defaultOptions);

    React.useEffect(() => {
        axios
            .get<BatteryOption[]>(apiURL+"api/v1/customerbatteryoption/?department_id=" + department_id, {
                headers: {
                    "Content-Type": "application/json"
                },
            })
            .then(response => {
                setOptions(response.data);
            });
        }, []);
        console.log('battery options data should be fetched at this point. department id = '+ String(department_id))
        console.log(options)

    //idがbattery_option_idにあるものを取得
    let fieldOptions:BatteryOption[] = [];
    if(options.length!=0){
        for (let i=0;i<options.length;i++){
            if(battery_option_id.includes(options[i].battery_option_id)){
                fieldOptions.push(options[i])
            }
        }
    }
    console.log('options in field status')
    console.log(fieldOptions)

    //充電器データの取得
    //charger optionデータの取得
    const defaultChargers:Charger[] = [];
    const [chargers, setChargers]: [Charger[], (chargers: Charger[]) => void] = React.useState(defaultChargers);

    React.useEffect(() => {
        axios
            .get<Charger[]>(apiURL+"api/v1/customercharger/?department_id=" + department_id, {
                headers: {
                    "Content-Type": "application/json"
                },
            })
            .then(response => {
                setChargers(response.data);
            });
        }, []);
        console.log('chargers data should be fetched at this point. department id = '+ String(department_id));
        console.log(chargers)

    //idがcharger_idにあるものを取得
    let fieldChargers:Charger[] = [];
    if(chargers.length!=0){
        for (let i=0;i<chargers.length;i++){
            if(charger_id.includes(chargers[i].charger_id)){
                fieldChargers.push(chargers[i])
            }
        }
    }
    console.log('chargers in field status')
    console.log(fieldChargers)

    return (
        <div>
        <Header
          drawerItems={[
            {
              title: "ホーム",
              path: "/",
            },
            {
              title: "バッテリー",
              path: "/units",
            },
            {
              title: "お客様",
              path: "/customer-view",
            },
          ]}
          //iconName={iconname}
        />
        <Center padding="15px" flexDirection="column">
            <Text
                w="270px"
                fontSize="18px"
                fontWeight="bold"
                margin="20px"
            >
                契約および製造リクエストが登録されました
            </Text>
            <Text
                w="270px"
                fontSize="14px"
                fontWeight="bold"
                margin="20px"
            >
                製造リクエストが製造担当者に通知されます。{"\n"}製造担当者が製造リクエストを元に製造情報を作成後、製造が開始されます。
            </Text>
            <StatusBar activeStep={4}/>
            <Divider/>
            {getContracts.map((value) => {
                    let date1:Date = new Date(String(value.execution_date))
                    let date2:Date = new Date(String(value.expiration_date))
                    return (
                        <ContractCardNoClick
                            contractName={value.contract_name}
                            contractNum={String(value.contract_id)}
                            contractType={value.contract_type}
                            startDate={formatDate(date1,DateFormat.YY_MM_DD_dd)}
                            endDate={formatDate(date2,DateFormat.YY_MM_DD_dd)}
                        />
                    );
                })}
            {/*
            <SubmitContractCard
                companyName={company}
                officeName={officeName}
                contractName={contractName}
                contractID={String(contract_id)}
                isLackData={true}
            />
            */}
            <Text
                w="260px"
                fontSize="11px"
                fontWeight="bold"
                color="#504E4E"
                marginTop="20px"
                marginBottom="10px"
            >
                バッテリーを載せる車体
            </Text>
            {fieldOptions.map((value,idx) => {
                let depth = 0                
                if(value.vertical.Int32>0){                 
                    depth=value.vertical.Int32
                }
                                        
                let width = 0
                if(value.horizontal.Int32>0){
                    width=value.horizontal.Int32          
                }
                                        
                let height = 0
                if(value.horizontal.Int32>0){
                    height=value.height.Int32
                }
                    return (
                        <Box marginBottom="20px">
                            <VehicleFieldCard3
                                vehicleName={value.option_name}
                                liftType={value.forklift}
                                volt = {value.voltage.Float64}
                                capacity={value.capacity.Float64}
                                weight={value.weight.Float64}
                                depth={depth}
                                width={width}
                                height={height}
                                num = {option_num[idx]}
                            />
                            {/*
                            <CarMiniCard2
                                //name={carNameState}
                                name = {value.this_name}
                                carnum = {value.this_num}
                                onRemoveHandler={() => {
                                    console.log("remove")
                                }}
                                noremove = {noremove}
                                noheader = {noheader}
                            />
                            */}
                        </Box>
                    );
            })}
            <Text
                w="260px"
                fontSize="11px"
                fontWeight="bold"
                color="#504E4E"
                marginBottom="10px"
            >
                製造する充電器情報
            </Text>
            {fieldChargers.map((value,idx) => {
                let provide =""
                if(value.how2supply=="switchboard"){
                    provide = "配電盤"
                }else if(value.how2supply=="plug"){
                    provide = "プラグ"
                }else{
                    provide = "その他"
                }
                    return ( 
                        <Box marginBottom="20px">
                            <ChargerFieldCard3
                                chargerName={value.charger_name}
                                provideMethod={provide}
                                amps={value.power_supply_ampere.Int32}
                                num = {charger_num[idx]}
                            />
                            {/*
                            <ChargerMiniCard2
                                //name={carNameState}
                                name = {value.this_name}
                                chargernum = {value.this_num}
                                onRemoveHandler={() => {
                                    console.log("remove")
                                }}
                                noremove = {noremove}
                                noheader = {noheader}
                            />
                            */}
                        </Box>
                    );
            })}
            <Button
                    w="260px"
                    h="40px"
                    margin="10px"
                    color="#504E4E"
                    bgColor="white"
                    border="2px"
                    borderColor="#504E4E"
                    onClick={() => {
                        history.push(`/`);
                    }}
                >
                    ホームへ
                </Button>
        </Center>
        </div>
    );
}

export default ContractRegistrationCompleteView2Page;